export default {
	data() {
		return {
			page:1,
			pagesize:6,
			userinfo:[],
			datalist:[],
			click_is:true
		}
	},
	onLoad() {
		uni.getStorage({
			key:'userinfo',
			success:(res)=>{
				this.userinfo = res.data
			}
		})
		this.getdata()
	},
	methods: {
		get_userinfo(){
			return new Promise((resolve,reject)=>{
				uni.getStorage({
					key: 'userinfo',
					success:(res)=>{
						this.userinfo = res.data
						resolve('suc')
					},fail() {
						resolve('err')
					}
				});	
				
			})
		},
		// 获取数据列表
		async getdata(){
			let _self = this
			await _self.get_userinfo()
			uni.request({
				url:_self.common.websiteUrl+"experhome_owners_waitvisit",
				header:{"user-token":"6a109faf305513d443337ddb1ad4cb9b"},
				method:"post",
				data:{'user_id':_self.userinfo.id,'page':_self.page,'pagesize':_self.pagesize},
				success: (res) => {
					if(res.data.code==200){
						_self.datalist = _self.datalist.concat(res.data.data);
					}else{
						_self.click_is = false
						_self.common.callback('暂无更多')
					}
				}
			});
			console.log('开始了')
		},
		qr(id){
			let _self = this
			uni.showModal({
			    title: '温馨提示',
			    content: '确认用户已到访?',
			    success: function (res) {
			        if (res.confirm) {
			           uni.request({
			           	url:_self.common.websiteUrl+"experhome_owners_confvisit",
			           	header:{"user-token":"6a109faf305513d443337ddb1ad4cb9b"},
			           	method:"post",
			           	data:{'id':id},
			           	success: (res) => {
			           		if(res.data.code==200){
			           			_self.common.callback('确认成功')
			           			let da = _self.datalist
			           			da.splice(_self.index, 1)
			           			_self.datalist = da;
			           			// _self.off()
			           		}else{
			           			_self.common.network()
			           		}
			           	}
			           });
			        }
			    }
			});
		},
		getimgurl(image){
			return this.common.websiteUrl+image
		},
		go_chat(){
			console.log('聊天区')
		},
		call_phone(phone){
			uni.makePhoneCall({
			    phoneNumber: phone
			});
		},
		back(){
			uni.navigateTo({
			    url: '../visitmanagement/visitmanagement'
			});
		}
	}
}
